<template>
  <div class="page-container m-share">
    <img src="./img/share.png" alt="立即分享" class="share-img">
    <h3 class="title">恭喜你！完成本次测评</h3>
    <p class="desc">分享给你的好友，再查看你的行测弱点<br>到底是哪一模块吧</p>
    <div class="submit-btn" @click="shareTo">立即分享</div>
  </div>
</template>

<script>
import { getUrlParam } from '@/assets/js/utils'
import { share } from '@/assets/js/config'

export default {
  name: 'share',
  created(){
    this.$nextTick(()=>{
      // app返回h5 带参数userId
      let userId = getUrlParam("userId")      
      if(userId){
        this.$router.replace({name:'result'})
      }
    })
  },
  methods:{
    // 立即分享
    shareTo(){
      let shareInfo = share
      var jsonShare = JSON.stringify(shareInfo)
      try{
        window.buzhi.getShareMsg(jsonShare)
      }catch(err){
        console.warn(err)
      }
      try{
        window.webkit.messageHandlers.getShareMsg.postMessage(jsonShare)
      }catch(err){
        console.warn(err)
      }
    }    
  }
}
</script>

<style lang="scss" scoped>
.m-share{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size:30px;
  .share-img{
    width:221px;
  }
  .title{
    font-size:34px;
    font-weight:bold;
    margin-top:60px;
    margin-bottom:24px;
  }
  .submit-btn{
    margin-top:56px;
    width:416px;
    height:98px;
    text-align: center;
    line-height: 98px;
    font-size:42px;
    color:#fff;
    border-radius:98px;
    background:linear-gradient(#fd7b58,#ff4343);
  }
}
</style>



